.main-interface{font-family:"Montserrat",sans-serif;font-weight:600;width:100%;padding:2rem;display:grid;place-items:center;height:auto;box-sizing:border-box}.NameZagolov{display:flex;justify-content:center;align-items:center;margin:10px 0;padding:5px 10px;text-align:center}.BlockLable{pointer-events:none;display:flex;align-items:center;margin:10px 0;padding:5px}.custom-select{width:100%;max-width:max;padding:8px;font-size:16px;border:2px solid #3498db;border-radius:6px;background-color:#fff;cursor:pointer;outline:none;transition:.3s;margin-bottom:10px}.custom-select:last-child{margin-bottom:0}.custom-select:hover{border-color:#2980b9}.damage-block{margin-top:25px;background:#f9f9f9;padding:10px;border-radius:8px}.select-container select:hover{border-color:#2980b9}.select-container select:focus{border-color:#bebebe;box-shadow:0 0 10px rgba(46,204,113,.5)}.select-container select::-ms-expand{display:none}.select-container::after{content:"▼";position:absolute;top:50%;right:15px;transform:translateY(-50%);color:#3498db;pointer-events:none}.button-container{margin-top:20px}.button{border:none;font-family:"Lato",sans-serif;font-size:16px;color:#fff;background:none;cursor:pointer;padding:12px 24px;display:inline-flex;align-items:center;justify-content:center;margin:10px 15px;text-transform:uppercase;letter-spacing:1px;font-weight:700;outline:none;position:relative;transition:all .3s;border-radius:6px;gap:8px}.btn-1{background:#3498db;color:#fff;position:relative;border-radius:6px}.btn-1:hover{background:#2980b9}.btn-1:active{background:#1f6694;top:2px}